// Header
$header                              : $secondary !default;
$header-link                         : $light-grey !default;
$header-link-active                  : $primary !default;
$user-btn                            : darken($header, 10%) !default;
$nav-spacing: 3.5px;

$project-nav-hover: $secondary !default;
$project-nav-active: $dropdown-link-hover-bg !default;

.whole_div{
  height:100vh;
  overflow-y: auto;
}
.download_cli{
  font: inherit;
  font-style: inherit;
  font-variant-ligatures: inherit;
  font-variant-caps: inherit;
  font-variant-numeric: inherit;
  font-variant-east-asian: inherit;
  font-weight: inherit;
  font-stretch: inherit;
  font-size: inherit;
  line-height: inherit;
  font-family: inherit;
}
.user{
  width: 35px;
  height: 35px;
  background-image: url('images/user.png');
}
.nav_toggle{
  z-index: 1;
  display:inline-block;
  position: fixed;
  left:25px;
  bottom: 30px;
  -webkit-appearance: none;
  color:white;
  outline: none;
  transform-style: preserve-3d;
  transition: 0.5s;
  background-image: url('images/arrow_right.png');
  width: 30px;
  height: 30px !important;
    &:checked{
    transform-origin: 75px 150px;
    transform: rotateY(180deg);
    transform-style: preserve-3d;

     & ~ .page-header-height{
      width:240px;
      transition: 0.5s;
      background-image: url('images/left_bar_background.png');
      background-repeat: no-repeat;
      .page-logo{
        border-bottom: none;
      }
      .nav-logo_2{
        display: inline-block;
        }
      .nav-logo_1{
        display:none;
        }
      .header-class{
        font-weight:bold !important;
        font-size:20px !important;
        color:white !important;
        }
      .nav_text{
        float: left;
        height:100%;
        color: white;
        font-size:13px;
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: 40px;
        width:calc(100% - 78px);
        text-align: left;
        opacity: 1;
        transition: all 0.3s ease-in-out;
        .description{
          top:50%;
          transform:translate(0, -50%);
          position: absolute;
        }
      }

    .awsom{
      display:inline-block;
    }
    + .whole_div{
      width:calc(100% - 240px);
      }
    }
  }
}

[data-tooltip-text]:hover {
	position: fixed;
}

[data-tooltip-text]:hover:after {
	background-color: #000000;
	background-color: rgba(0, 0, 0, 0.8);

	-webkit-box-shadow: 0px 0px 3px 1px rgba(50, 50, 50, 0.4);
	-moz-box-shadow: 0px 0px 3px 1px rgba(50, 50, 50, 0.4);
	box-shadow: 0px 0px 3px 1px rgba(50, 50, 50, 0.4);

	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;

	color: #FFFFFF;
	font-size: 10px;
	content: attr(data-tooltip-text);

	left:30px;    
	padding: 7px 12px;
	position: fixed;
  width: auto;
	min-width: 50px;
	word-wrap: break-word;

	z-index: 1500;
}


.page-header-height {
    @include no-select;
    margin-bottom: 0px;
    float: left;
    width: 78px;
    min-height:100%;
    text-align: center;
    background-color: $primary;
    transition: 0.5s;
    // box-shadow: 10px 0 10px 3px $shadow;
    position:fixed;
    user-select:none;

    NAV {

      .scope-class > a {
        width: 100%;
        height: 100%;
        display:inline-block;
      }
      .scope-class > a.active {
        background:rgba(100, 98, 98, 0.486);
      }
      
      .scope-class{
        width:100%;
        height:40px;
        background-color: transparent;
        vertical-align: middle;
        position: relative;
        &:hover{
          background-color: rgba(209, 203, 203, 0.486);
        }
      }

      .etc_class{
        width:100%;
        height:40px;
        background-color: transparent;
        vertical-align: middle;
        position: relative;
        &:hover{
          background-color: rgba(209, 203, 203, 0.486);
        }
        > dd{
            margin: 0;
            padding:0;
            display:none;
            li{
              list-style-type:none;
              margin: 0;
              padding:0;
              .nav_text{
              text-align: left;
              opacity: 0;
              transition: 0.5s ease-in-out;
            }
          }
        }
      }
      overflow:hidden;
      font-size: .9em;
      box-sizing: border-box;
      display:flex;
      width:100%;
      height:100vh;
      flex-direction: column;

      align-items: center;
      .nav_text{
        font-weight: bold;
        color:white;
        transition: all 0.3s ease-in-out;
        opacity: 0;
        font-size: 0;
        width:0;
      }
      .page-logo{
        width:100%;
        height:94px;
        border-bottom: 1px solid white;
      }

      .icon_text{
        display: inline-block;
        width:78px;
        height:100%;
        float: left;
        .scope-global {
          width:24px;
          height:24px;
          background-image: url('images/ScopeGlobal.png');
          background-repeat: no-repeat;
          display: inline-block;
          transform: translate(-50%, -50%);
          top:50%;
          position: absolute;
           }
        .scope-cluster {
          width:24px;
          height:24px;
          background-image: url('images/ScopeCluster.png');
          background-repeat: no-repeat;
          display: inline-block;
          transform: translate(-50%, -50%);
          top:50%;
          position: absolute;
        }
        .scope-project {
          width:24px;
          height:24px;
          background-image: url('images/ScopeProject.png');
          background-repeat: no-repeat;
          display: inline-block;
          transform: translate(-50%, -50%);
          top:50%;
          position: absolute;
        }
        >img{
          transform:translate(0, 50%);
        }

      }

      .nav_toggle{
        width: 100%;
        height: 80px;
        bottom: 0;
      }
    }
    .nav-open-menu{
      height:50px;
      width:100%;
      bottom:20px;

    }
    .scope-list{
      height:100%;
      width:100%;
      display:flex;
      flex-direction: column;
      max-height: calc(100% - 173px);
      overflow-y:auto;
      border-bottom: 1px solid white;
      border-top: 1px solid white;
      overflow-x:hidden;
    }
    .scope-list::-webkit-scrollbar{
      width:6px;

    }
    .scope-list::-webkit-scrollbar {
      width: 6px;
    }
    .scope-list::-webkit-scrollbar-track {
      background-color: transparent;
    }
    .scope-list::-webkit-scrollbar-thumb {
      border-radius: 3px;
      background-color: gray;
    }
    .scope-list::-webkit-scrollbar-button {
      width: 0;
      height: 0;
    }
    .logo{
      width:100%;
      text-align: center;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
    .nav-logo_1 {
      width: 41px;
      height: 27px;
      display:inline-block;
      background-image: url('images/logos/egov_menu_close.png');
      background-repeat: no-repeat;
      position: relative;
      top:50%;
      margin-top: -29px;
    }
    .nav-logo_2 {
      width: 160px;
      height:39px;
      vertical-align: middle;
      background-image: url('images/logos/egov_menu_open.png');
      background-repeat: no-repeat;
      position: relative;
      top:50%;
      margin-top: -25px;
      display: none;
    }
    .scope-header-class{
      text-align: center;
      width:100%;
      height:40px;
      vertical-align: middle;
      position: relative;
      background-image:url('images/left_bar_title.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;;

      }
    .scope{
      width:100%;
    }
    .awsom{
      color:white;
      position: absolute;
      transform: translate(0, 80%);
      right:10%;
      display:none;
      background-image: url('images/arrow_left.png');
      background-repeat: no-repeat;
      width: 13px;
      height: 13px;
    }

    dd {
      margin:0;
      display:none;
      width:100%;
    }

    dt:hover{
      cursor: pointer;
    }
    dt.open{
      background-color: rgba(182, 182, 182, 0.1);
      transition: 0.3s;
      & + dd{
        transition:0.3s;
        display:inline-block;
      }
      > i{
        transform:translate(0, 100%) rotate(-90deg);
      }
    }
    
    .etccc{
      background-image: url('images/logos/etc.png');
      display:inline-block;
      width:4px;
      height:21px;
      background-repeat: no-repeat;
    }

  }
  // Page-Header-Width
  .page-header-width {
    @include no-select;
    margin-bottom: 0px;
    user-select:none;

    NAV {
      font-size: .9em;
      width: 100%;
      box-sizing: border-box;
      display: -ms-flexbox;
      display: -webkit-box;
      display: flex;
      flex: 0 1 auto;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: center;
      height:95px;
      font-family: NotoSansCJKkr;
      font-size: 13.5px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      letter-spacing: 0.34px;
      color: $primary;

      > ul {
        border-radius: 3px 0 0 3px;
        padding: 0;
        vertical-align: middle;
        height: 100%;
        float:left;
        div > .ember-view, .ember-basic-dropdown {
          height: 100%;
          &.ember-basic-dropdown-content {
            height: max-content;
            max-width: 250px !important;
          }
        }

        .cluster-while{
          .ember-view{
            height:100%;
            &.ember-basic-dropdown-content{
              height: max-content;
              border-radius: 16px;
            }
          }
        }
        .ember-view{
          height:100%;
          &.ember-basic-dropdown-content{
            height: max-content;
          }
        }
        
        &.nav-main {
          z-index: 1;
          flex-grow: 1;
          margin-block-start: 0px;  //추가 기입
          margin-block-end: 0px;  //추가 기입

          &:hover{
            .cluster_list{
              border-radius: 16px;
              outline: none;
              display:block;
            }
          }
        }
        >div > div > li {
          float:left;
          margin-left: 3%;
          list-style-type: none;
          &.active {
            text-decoration: none;;
            background-color: $primary;
            border-radius: 16px;
            >a {
              color:white;
            }
          }
          &:hover{
            text-decoration: none;
            border-radius: 16px;
            box-shadow: 0px 5px 7.6px 0.4px $shadow;
            background-color: $primary;
            >a{
              text-decoration: none;
              color:white;
            }
          }

          .ember-basic-dropdown-trigger a {
            color: $header-link;
            display: block;
            &:hover {
              color: white;
              text-decoration: none;
            }
            .icon-chevron-down {
              font-size: .75em;
            }
          }
        }
      }

      .cluster-scope{
        color:white;
        width: 145px;
        height: 75px;
        border-radius: 16px;
        background-color: $primary;
        text-align: center;
        position: relative;
        display:inline-block;
        .cluster{
          width: 26px;
          height: 26px;
          background-image: url('images/arrow_down.png');
          position: absolute;
          top: 30%;
          left: 50%;
          transform: translate(-50%, -55%);
        }
      }

      .clusterName{
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        display:block;
        font-size: medium;
        max-width: 145px;
        white-space: normal;
        line-height: 1;
      }

      .cluster-choice{
        color:white;
        width: 145px;
        height: 65px;
        border-radius: 16px;
        background-color: $primary;
        text-align: center;
        position: relative;
        display:inline-block;
        transform:translate(0, 22.5%);

        >i{
          transform: translate(0, 25%);
        }
        .cluster{
          width: 26px;
          height: 26px;
          background-image: url('images/arrow_down.png');
          position: absolute;
          position: absolute;
          top: 30%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }

      .cluster-while{
        text-align:center;
        background-color:white;
        float:left;
        height: 100%;
        width:145px;
      }

      .cluster_list{
        padding-left: 0px;
        text-decoration: none;
        display:block;
        border-radius: 16px;
        box-shadow: 0px 5px 19.7px 1.3px $shadow;
        background-color: #ffffff;
        font-family: NotoSansCJKkr;
        font-size: 16px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        letter-spacing: 0.16px;
        text-align: center;
        color:$primary;
        background-color:white;
        width:145px;
        outline: none;
        display:none;
        list-style-type:none;
        >li{
          width: 145px;
          height: 35px;
          list-style-type:none;
          outline: none;
          &:hover{
            background-color: $primary;
            outline: none;
            text-decoration: none;
            text-align: center;
           >a{
             color:white;
           }
          }
          &.active{
            text-decoration: none;
            text-align: center;
            background-color: $primary;
            >a {
              color:white;
            }
          }
        }


        >li:first-child{
          border-radius: 16px 16px 0 0;
        }
        >li:last-child{
          border-radius: 0 0 16px 16px;
        }
      }
      .cluster-scope
      .ember-basic-dropdown-content {
        outline: none;
        
        body.touch & {
          position: static;
        }

        font-size:  15px;
      }

      .project_toggle{
        height:100%;
        float: left;
        max-width:80%;
        margin-left: 1%;
        padding-right: 1%;
        width:auto;
      }

      .open_project{
        float: left;
        transform: translate(0, 125%);
        outline: none;
        -webkit-appearance: none;
        outline: none;
        background-image: url('images/arrow_right_blue.png');
        width: 26px;
        height: 26px;

        &:checked{
          transform: rotateY(-180deg) translate(0, 125%);
          transition: 0.3s;
          & ~ .project_lists{
            display:flex;
          }
        }
      }

      .open_project_red {
        float: left;
        transform: translate(0, 125%);
        outline: none;
        -webkit-appearance: none;
        outline: none;
        background-image: url('images/arrow_right_red.png');
        width: 26px;
        height: 26px;

        &:checked{
          transform: rotateY(-180deg) translate(0, 125%);
          transition: 0.3s;
          & ~ .project_lists{
            display:flex;
          }
        }
      }

      .project_lists{
        float:left;
        overflow-y:auto;
        transition: 0.5s;
        display:none;
        height:100%;

        // padding-right: 1%;
        > li{
          transition: 0.5s;
          float:left;
          margin-left:9px;
          // height: 100%;
          transform:translate(0, 60%);
          padding:5px 16px;
          max-height:42px;
        }
      }
      .project_lists::-webkit-scrollbar{
        width:6px;
  
      }
      .project_lists::-webkit-scrollbar {
        width: 6px;
      }
      .project_lists::-webkit-scrollbar-track {
        background-color: transparent;
      }
      .project_lists::-webkit-scrollbar-thumb {
        border-radius: 3px;
        background-color: gray;
        // background-color: darken( $bg, 10%);
      }
      .project_lists::-webkit-scrollbar-button {
        width: 0;
        height: 0;
      }

      .cluster_link{
      text-align: center;
      display: table;
      width: 100%;
      transform: translate(0, 25%);
      }
      .project_list{
        border-radius: 16px;
        box-shadow: 0px 5px 5px 0.4px $shadow;
        background-color: #ffffff;
        font-family: NotoSansCJKkr;
        font-size: 14.5px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: 2.1;
        letter-spacing: 0.15px;
        text-align: center;
        color: $primary;
        }
      .ember-basic-dropdown-trigger {
        a {
          text-decoration: none;
        }
      }

      .dropdown-menu {
        border: 0;
        z-index: 4;
        padding: 0;
        top: auto!important;
        border-radius: 0;
        box-shadow: $darken-helper 0 0 7px;
      }
      .fa-user{
        color: #74798C;
      }
      .login-user{
        padding-right:10px;
        color: #74798C;
      }
    }

    .accodion{
      display: none;
    }
    .nav-user {
      border-left: 1px solid #eee;
      border-radius: 0 3px 3px 0;
      margin: 0;
      position: relative;
      z-index: 4;
      
      .ember-basic-dropdown-content{
        right: 15%;
        width: 500px;
        margin-top: 20%;
      }
      .nav-item {
        top:50%;
        transform:translate(0, -50%);
        a.nav-link {
          padding: 0;
          display: inline-block;
          border: none;
        }
      }

      > li > a {
        display: block;
        padding: 4px 10px;

        &:hover {
          border-bottom-color: transparent;
        }
      }

      .user-auth {
        line-height: initial;
        padding: 10px 20px 10px 10px;
      }

      > div:hover {
      }
    }

    .nav-item {
      position: relative;
    }


    .nav-cluster {
      margin-left: 3%;//추가
      outline: none;
      height:100%;
      a {
        &:hover {
          text-decoration: none;
        }

        &.text-white:hover {

          &::after {
            border-left-color: mix($user-btn, black, 80%);
          }
        }
      }

      .project-menu .global ul li a:hover {
        background: transparent;
      }


      .ember-basic-dropdown-trigger {
        max-width: 300px;
        @include clip;
        @include hand;
        outline: none;

        > a {
          &.two-line {
            padding-top: 1px;
            padding-bottom: 0;
          }

          &:focus {
            text-decoration: none;
          }
        }

      }

      .chevron {
        color: white;
        position: absolute;
        right: 10px;
        top: 21px;
      }
    }

    .ember-basic-dropdown-trigger {
      &.active {
        > a {
          text-decoration: none;
          margin: 0;
        }
      }

      > a {
        color: $header-link;
        display: block;
        .icon-chevron-down {
          font-size: .75em;
        }
      }

    }

    .quick-add {
      display:table-cell;
      border-radius: 0;
      margin: 0;
      padding: 0;
      li {
        display: block;
        a {
          display: block;
        }
      }
    }


    .bg-error.badge-xs {
      position: absolute;
      right: 4px;
      top: 15px;
      text-align: center;
      width: 19px;
      border-radius: 90%;

      a {
        color: white;
      }
    }

    .project-link {
      position: relative;
      .default-project {
        position: absolute;
        font-size: 10px;
        top: 8px;
        color: $warning;

        &.nested {
        }
      }
    }
  }
