// -----------------------------------------------------------------------------
// This file contains all styles related to the header of the site/application.
// -----------------------------------------------------------------------------
header {
  clear: both;
}
.login_header {
  border-bottom: solid 2px $border;
  margin: 0 0 16.5px 0;
  padding: 15px 0;

  h1 {
    margin: 2px 0;
    font-size: 21px;
  }

  .right-buttons {
    @include clearfix;
    float: right;

    > .resource-actions {
    .btn {
      padding: 6px 10px 4px 10px;
      margin-left: 10px;
      }
    }
  }
}

.header {
    display: grid;
    grid-template-areas:
        "type-banner type-banner"
        "title actions"
        "state-banner state-banner";
    grid-template-columns: auto auto;
    margin-bottom: 20px;
    // align-content: center;
    min-height: 48px;
    align-items: center;

    > * {
      flex-grow: 1;
    }

    h1 {
      margin: 0;
    }
  .right-buttons {
    @include clearfix;
    justify-content: flex-end;
    align-self: center;
    text-align: right;
    // float: right;
  }
}

.header-borderless {
  @extend .header;
  border-bottom: none;
}
