HTML {
  height: 100%;
}

BODY {
  direction: ltr;
  background-color : $body-bg;
  min-height       : 100%;
  // font-weight      : normal;
  position         : relative;
  height: 100%;
  width: 100%;

  &.no-spin .icon-spin {
    -webkit-animation : none;
    animation         : none;
  };
}
BODY > .ember-view{
  width: 100%;
  outline: none;
}

#application {
  padding-top: 10px;
  padding-bottom: 70px; // So the footer doesn't cover the end of the body
  min-height: 150px; // header + footer + a bit
  padding-top: 0px;
  height:100%;
  padding-bottom: 0px;
  min-height:150px;
}

BODY.popup #application {
  padding-bottom: 0;
  min-height: 0;
  max-width: initial;
}

MAIN {
  position: relative;
  padding:2% 2% 0 2%;
  // section태그와 본문의 간격 조정
  > section {
    padding: 15px 0 !important;
  }
}
.whole_div{
  width:calc(100% - 78px);
  position:fixed;
  right:0;
  top:0;
  transition: width .5s;
}
.main-header{
  height:10%;
  width:100%;
  float:left;
  background-color: green;
}

.footer-actions {
  padding-top: 10px;
  text-align     : center;
  position       : relative;
}

.modal-container .footer-actions {
  margin-bottom: 0;
}

.box {
  background: $accent-bg;
  padding: 20px;
  border: solid 1px $accent-border;

  hr {
    border-color: darken($border, 5%);
  }

  > h4 {
    border-bottom: solid 2px darken($border, 5%);
    margin-bottom: 20px;
    padding-bottom: 15px;
    text-transform: capitalize;
  }

  &.bg-secondary {
    @extend .bg-secondary;

    hr {
      opacity: .3;
    }
  }

  .accordion {
    background: $accent-bg;
  }

  .text-muted {
    color: $mid-grey;
  }

  .bg-disabled {
    background-color: $bg-disabled;
  }

  .over-hr {
    & > span:before,
    & > span:after {
      background: #c5cbce;
    }
  }
}
